<template>
  <div class="w-30 h-100 right flex flex-column">
    <div class="h-50 m-b">
      <dv-border-box-13>
        <div class="item w-100 h-100">
          <div class="w-100 h-10 font-weight font-s"></div>
          <div class="w-100 h-90 p-tb" id="echart1"></div>
        </div>
      </dv-border-box-13>
    </div>
    <div class="h-50">
      <dv-border-box-13>
        <div class="item w-100 h-100">
          <div class="w-100 h-15 font-weight font-s"></div>
          <dv-scroll-board :config="config" class="w-100 h-85" />
        </div>
      </dv-border-box-13>
    </div>
  </div>
</template>

<script>
export default {
  name: "right",
  data() {
    return {
      config: {
        startCharts:"",
        
        header: ["1", "2", "3"],
        headerBGC: "#018DCE",
        evenRowBGC: "#000D4A",
        rowNum: 7,
        data: [
          ["11", "12", "13"],
          ["21", "22", "23"],
          ["31", "32", "33"],
          ["41", "42", "43"],
          ["51", "52", "53"],
          ["61", "62", "63"],
          ["71", "72", "73"],
          ["81", "82", "83"],
          ["91", "92", "93"],
          ["101", "102", "103"]
        ]
      }
    };
  },
  mounted() {
    var _this = this;
    this.echart();


  },
  methods: {
    
    echart() {
     
        var isSet = true; // 
      var _this = this;
      var charPiecurrentIndex = 0;
      var oldindex=0;
      
      var chart = this.$echarts.init(document.getElementById("echart1"));
      var option = {
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          // orient: 'vertical',
          // top: 'middle',
          type: "scroll",
          orient: "vertical",
          right: 10,
          top: 0,
          bottom: 20,
          left: "left",
          textStyle: {
            color: "#fff",
            fontSize: 12
          }
        },
        grid: {
          x: "-10%",
          y: 40,
          x2: 20,
          y2: 20
        },
        color: ["#09d0fb", "#91CC75", "#95f8fe", "#f9f390", "#ecfeb7"],
        series: [
          {
            type: "pie",
            radius: "75%",
            center: ["50%", "50%"],
            selectedMode: "single",
            data: [
              { value: 1548, name: "" },
              { value: 535, name: "" },
              { value: 510, name: "" },
              { value: 634, name: "" },
              { value: 735, name: "" }
            ],
            label: {
              //
              normal: {
                position: "outside",
                formatter: "{b}: {d}%",
                textStyle: {
                  fontSize: 12
                }
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
     
      chart.setOption(option);
   
      // 
      chart.on("mouseover", function(param) {
        isSet = false;
        clearInterval(_this.startCharts);
        console.log("charPiecurrentIndex"+charPiecurrentIndex)        
        console.log("dataIndex"+param.dataIndex)        

        //
        chart.dispatchAction({
          type: "downplay",
          seriesIndex: 0,
          dataIndex: charPiecurrentIndex
        });
        charPiecurrentIndex = param.dataIndex
        //
        chart.dispatchAction({
          type: "highlight",
          seriesIndex: 0,
          dataIndex: param.dataIndex
        });
        //tooltip
        chart.dispatchAction({
          type: "showTip",
          seriesIndex: 0,
          dataIndex: param.dataIndex
        });
      });

      chart.on("mouseout",function(param){
        
      })

      //
      window.addEventListener("resize", function() {
        chart.resize();
      });


    
      //
      var chartHover = function() {
    console.log(charPiecurrentIndex) 
        var dataLen = option.series[0].data.length;

        // 
        chart.dispatchAction({
          type: "downplay",
          seriesIndex: 0,
          dataIndex: charPiecurrentIndex
        });
        charPiecurrentIndex = (charPiecurrentIndex + 1) % dataLen;
         
        // 
        chart.dispatchAction({
          type: "highlight",
          seriesIndex: 0,
          dataIndex: charPiecurrentIndex
        });
        //  tooltip
        chart.dispatchAction({
          type: "showTip",
          seriesIndex: 0,
          dataIndex: charPiecurrentIndex
        });
      };

      _this.startCharts = setInterval(chartHover, 2000);
      // 4
      chart.on("mouseout", function(param) {
        if (!isSet) {
          _this.startCharts = setInterval(chartHover, 2000);
          isSet = true;
        }
      });
    }
  }
};
</script>


<style scoped>
.item {
  padding: 0.33rem 0.2rem 0.2rem;
}
</style>



